<template>  
  <div>  
    <h1>项目问题管理</h1>  
    <el-row>
      <input type="text" v-model="searchText" placeholder="输入问题ID或问题类型" style="margin-right: 20px;">  
      <el-button type="primary" plain @click="search" size="small">查找问题</el-button>
      <el-button type="primary" plain @click="reset" size="small">重置</el-button>
    </el-row>
    <div v-if="problems.length">  
      <ul>  
        <li v-for="problem in problems" :key="problem.id">  
          <h2>{{ problem.id }} - {{ problem.type }}</h2>  
          <p>问题描述：{{ problem.description }}</p>  
          <p>修正意见：{{ problem.fix }}</p>  
          <p>代码范例：{{ problem.example }}</p>  
        </li>  
      </ul>  
    </div>  
    <div v-else>  
      <p>没有找到任何问题。</p>  
    </div>  
  </div>  
</template>  
  
<script>  
export default {  
  data() {  
    return {  
      searchText: '',  
      problems: [  
        {  
          id: 'P001',  
          type: '类型1',  
          description: '问题1的描述',  
          fix: '修正意见1',  
          example: '代码范例1'  
        },  
        {  
          id: 'P002',  
          type: '类型2',  
          description: '问题2的描述',  
          fix: '修正意见2',  
          example: '代码范例2'  
        },  
        // 其他问题数据...  
      ]  
    };  
  },  
  methods: {  
    search() {  
      const regex = new RegExp(this.searchText, 'i');  
      this.problems = this.problems.filter(problem => {  
        return regex.test(problem.id) || regex.test(problem.type);  
      });  
    },
    reset(){
      this.$router.go(0)
    }
  }  
};  
</script>